import http from '../../../../configs/httpConfig'
import {notifyError, notifySuccessadd, notifySuccessdelete, notifyWarning} from "../../../Toast"

export const getAllQuizDataInit = () => ({
  type: 'GET_ALL_DATA_INIT'
})

// ** Get all Data
export const getAllData = (params, page) => {
  return async (dispatch) => {
    
    dispatch(getAllQuizDataInit())
    await http.get(`/quiz/list?page=${page}`, { params }).then((response) => {
      dispatch({
        type: 'GET_ALL_DATA',
        data: response.data.data,
        params
      })
      console.log(response.data.data)
    })
  }
}

