// ** Add new quiz
export const addQuiz = (quiz) => {
  return (dispatch) => {
    http
      .post('/quiz/add', quiz)
      .then((response) => {
        notifySuccessadd(response.data.message)
        dispatch({
          type: 'QUIZ_ADD',
          data: response.data
        })
      })
      .catch((err) => {
        notifyError(err.message)
        console.log(err)
      })
  }
}

export const addQuizInit = () => ({
  type: 'ADD_QUIZ_POST_INIT'
})

export const addQuestion = (question) => {
  return (dispatch) => {
    dispatch(addQuizInit())
    http
      .post('/quiz/questions/add', question)
      .then((response) => {
        notifySuccessadd(response.data.message)
        dispatch({
          type: 'QUIZ_QUESTION_ADD',
          data: response.data
        })
        
      })
      .catch((err) => {
        notifyWarning(err.message)
        console.log(err)
      })
  }
}

export const resetQuizQuestions = () => ({
  type: 'RESET_QUESTIONS_ADDED'
})

export const deleteQuiz = (id) => {
  return (dispatch) => {
    http
      .delete(`/quiz/delete/${id}`)
      .then(() => {
        notifySuccessdelete()
        dispatch(getAllData())
      })
      .catch((err) => {
        notifyError(err)
        console.log(err)
      })
  }
}


