export const quiztogglestatus = (id, status) => {

  return async (dispatch) => {
   await http.post(`quiz/toggle_status/${id}`).then((response) => {
      notifySuccessadd(response.data.message)
      console.log(status)
    dispatch({
        type: 'QUIZ_TOGGLE_STATUS',
        data: status
      })  
     
    }).catch((e) => {
      
      console.log("Error in Feed Post: ", e)
      
    })
  }
}