import React, { useEffect, Fragment, useState } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import classnames from 'classnames'
import Select from 'react-select'
import Repeater from '@components/repeater'
import Flatpickr from 'react-flatpickr'
import { useRouteMatch } from 'react-router-dom'
import { useForm, Controller } from 'react-hook-form'
import { selectThemeColors } from '@utils'
import {  Plus } from 'react-feather'
import BreadCrumbs from '@components/breadcrumbs'
import {
  Form,
  Label,
  Input,
  FormGroup,
  Row,
  Col,
  Button,
  Card
  //ButtonGroup
} from 'reactstrap'

// import { Plus } from 'react-feather'
import {
  getQuizQuestions,
  resetQuizEditQuestions,
  getquizDetails,
  quizEditQuestions,
  quizEditQuestionsEach
  // getAllData
} from '../store/action'

import '@styles/react/libs/flatpickr/flatpickr.scss'
import '@styles/react/libs/react-select/_react-select.scss'

const colourOptions = [
  { value: 'Cricket', label: 'Cricket' },
  { value: 'Football', label: 'Football' },
  { value: 'Basketball', label: 'Basketball' }
]
const QuizEdit = () => {
  const route = useRouteMatch()
  // const getFilterQuiz =[]
  const dispatch = useDispatch()
  const [language, setLanguage] = useState()
  const [lang, setLang] = useState(1)
  const id = route.params.id
  const store = useSelector((state) => state.quiz)
  const [count, setCount] = useState(1)
  const { isLoading, quizQuestionsData, quizDetailsData } = store
/////////////////////////////
  const { register, errors, handleSubmit, control, setValue, trigger } =
    useForm({
      shouldUnregister: false
    })
  // console.log(quizQuestionsData)
  
  useEffect(() => {
    if (id) {
      dispatch(getQuizQuestions(id))
      dispatch(getquizDetails(id))
    }
    return () => {
      dispatch(resetQuizEditQuestions())
    }
  }, [])

  console.log(store.quizQuestionsData)
  console.log(store.quizDetailsData)
console.log(language)
  
  // useEffect(() => {
  //   if (quizdet) {
  //     setQuizDetails(quizdet)
  //     setValue('quiz_name', quizdet.translation_quiz[0].translation_quiz_name)
  //     // setValue('quiz_name', quizdet.translation_quiz.map(quiz => quiz.transl))
  //     setValue('quiz_category', {
  //       value: quizdet.quiz_category,
  //       label: quizdet.quiz_category
  //     })
  //     setValue('quiz_expiry', quizdet.quiz_expiry)
  //     setValue('quiz_coins', quizdet.quiz_coins)
  //   }
  //   // if (quizdet?.translation_quiz?.length === 1) {
  //   //   setQuizDetails(quizdet)
  //   // } else if (quizdet?.translation_quiz?.length > +1) {
  //   //   const a = quizdet?.translation_quiz?.filter((lan) =>  (lan.master_language_code === language))
  //   //   setQuizDetails(a)
  //   // } else {
  //   //   setQuizDetails(quizdet)
  //   // }
  //    // console.log(quizdet.translation_quiz)
  // }, [JSON.stringify(quizdet), language])

  useEffect(() => {
    if (quizQuestionsData.length) {
      setCount(quizQuestionsData.length)
      
      quizQuestionsData.forEach((quizD, ind) => {
        //setValue(`question-${ind}`, quizD.translation_quiz_questions[0].translation_quiz_question)
        //setValue(`question-${ind}`, quizD.translation_quiz_questions[0].translation_quiz_question)
     //   const ss = quizDetailsData.translation_quiz.filter((qs => qs.master_language_id === lang))
        const setEnglish = quizD.translation_quiz_questions.filter((qz) => qz.master_language_id === lang) //set Defalut English
        if (setEnglish.length) {
          setValue(`question-${ind}`, setEnglish[0].translation_quiz_question)
        } else {
          quizD.translation_quiz_questions.forEach((lang) => {
            if (setLang === lang.master_language_id) {
              setValue(`question-${ind}`, quizD.translation_quiz_questions[0].translation_quiz_question)
            }
          })
        }

        quizD.Answers.forEach((qAns, i) => {
        
        //setValue(`answer${i + 1}-${ind}`, qAns.translation_quiz_answers[0].translation_quiz_answer)
          console.log('quizdetails', quizDetailsData)
          console.log('quizquestion', quizQuestionsData)
          const setEnglish = qAns.translation_quiz_answers.filter((qz) => qz.master_language_id === lang) //set Defalut English
          if (setEnglish.length) {
            setValue(`answer${i + 1}-${ind}`, setEnglish[0].translation_quiz_answer)
          } else {
            quizD.translation_quiz_questions.forEach((lang) => {
              if (setLang === lang.master_language_id) {
                setValue(`answer${i + 1}-${ind}`, qAns.translation_quiz_answers[0].translation_quiz_answer)
              }
            })
          }
        
          if (qAns.quiz_answer_id === quizD.quiz_answer_id) {
            setTimeout(() => {
              const dc = document.querySelectorAll(
                `input[name="correct-answer-${ind}"]`
                )
                if (dc.length) {
                  dc[i]?.setAttribute('checked', true)
                }
            }, 100)
          }
        })
      })
    }
  }, [JSON.stringify(quizQuestionsData), lang])

  useEffect(() => {
    console.log('try', quizDetailsData.translation_quiz)
    //const setEnglish = quizDetailsData.translation_quiz.filter((qz) => qz.master_language_id === lang)
    // if (setEnglish.length) {
    //   setValue('translation_quiz_name', setEnglish[0].translation_quiz_name)
    // } else {
    //   quizDetailsData.translation_quiz_questions.forEach((lang) => {
    //     if (setLang === lang.master_language_id) {
    //       setValue(`translation_quiz_name`, 'hell')
    //       //qAns.translation_quiz_answers[0].translation_quiz_answer
    //     }
    //   })
    // }
    //code for quiz name
    if (quizDetailsData.length) {
    quizDetailsData.forEach((quizD) => {
      const setEnglish = quizDetailsData.translation_quiz.filter((qs => qs.master_language_id === lang))
      console.log(setEnglish[0].translation_quiz_name)
      if (setEnglish.length) {
        setValue('translation_quiz_name', setEnglish[0].translation_quiz_name)
      } else {
        quizD.translation_quiz_questions.forEach((lang) => {
          if (setLang === lang.master_language_id) {
            setValue(`translation_quiz_name`, 'hell')
          }
        })
      }
    })
  }
    if (Object.keys(quizDetailsData).length) {
      setValue('quiz_category', {
        value: quizDetailsData.quiz_category,
        label: quizDetailsData.quiz_category
      })
      setValue('quiz_expiry', quizDetailsData.quiz_expiry)
      setValue('quiz_coins', quizDetailsData.quiz_coins)
      setValue('quiz_name', quizDetailsData.translation_quiz[0]?.master_language_id === lang ? quizDetailsData.translation_quiz[0]?.translation_quiz_name : quizDetailsData.translation_quiz[1]?.translation_quiz_name)
      // setValue('quiz_name', quizDetailsData.translation_quiz[0].translation_quiz_name)
      // setValue('translation_quiz_name', quizDetailsData[0].translation_quiz_name)
    }
  }, [JSON.stringify(quizDetailsData), lang])

  const submitQuestion = (id) => {
    trigger().then((res) => {
      if (res) {
        const questionID = quizQuestionsData[id]

        const param = {
          quiz_id: questionID.quiz_id,
          // quiz_question: document.getElementById(`question-${id}`).value,
          translation_quiz_questions:[
            {
              translation_quiz_question: document.getElementById(`question-${id}`).value
            }
          ], 
          quiz_question_duration: '30',
          quiz_question_sno: id,
          quiz_answer_id: questionID.Answers[0].quiz_answer_id,
          quiz_question_answers: [
            {
              quiz_answer: document.getElementById(`answer1-${id}`).value,
              quiz_answer_sno: '1',
              quiz_answer_id: questionID.Answers[0].quiz_answer_id,
              quiz_answer_correct: 1
              // quiz_answer_correct:
              //   document.querySelector(
              //     `input[name="correct-answer-${id}"]:checked`
              //     ).value === '1'
              //     ? 1
              //     : 0
                },
                {
                  quiz_answer: document.getElementById(`answer2-${id}`).value,
                  quiz_answer_sno: '2',
                  quiz_answer_id: questionID.Answers[1].quiz_answer_id,
                  quiz_answer_correct: 0
                  // document.querySelector(
                  //   `input[name="correct-answer-${id}"]:checked`
                  //   ).value === '2'
                  //   ? 1
                  //   : 0
                  },
                  {
                    quiz_answer: document.getElementById(`answer3-${id}`).value,
              quiz_answer_sno: '3',
              quiz_answer_id: questionID.Answers[2].quiz_answer_id,
              quiz_answer_correct: 0
              // document.querySelector(
              //   `input[name="correct-answer-${id}"]:checked`
              //   ).value === '3'
              //   ? 1
              //   : 0
              },
              {
                quiz_answer: document.getElementById(`answer4-${id}`).value,
                quiz_answer_sno: '4',
                quiz_answer_id: questionID.Answers[3].quiz_answer_id,
                quiz_answer_correct: 0
                // document.querySelector(
                //   `input[name="correct-answer-${id}"]:checked`
                //   ).value === '4'
                //   ? 1
                //   : 0
                }
              ]
        }

        dispatch(quizEditQuestionsEach(questionID.quiz_question_id, param))
        console.log(param)
      }
    })
  }
 
  const increaseCount = () => {
    if (count === 5) return
    setCount(count + 1)
  }
  const onSubmit2 = () => {}
  const changelang = (id) => {
    setLang(id)
  }
  
  const onSubmit1 = (val) => {

    // const del = data.filter((qz) => qz.quiz_id === route.params.id)
    // const quiznam = del.translation_quiz[0].translation_quiz_name
    
    const params = {
      quiz_category:val.quiz_category.value,
      quiz_expiry: val.quiz_expiry,
      quiz_coins: val.quiz_coins,
      translation_quiz_:val.quiz_name
      // translation_quiz:[
      //   {
      //     translation_quiz_name:val.quiz_name
      //   }
      // ]
    }
//     const name = translation_quiz[0].translation_quiz_name
//     const param = {name: val.quiz_name }  
// console.log(params)
    dispatch(quizEditQuestions(id, params))
  }

  if (isLoading) {
    return <div>Loading</div>
  }

  // if (!quizQuestionsData.length && !Object.keys(quizDetails).length) {
  //   return <div>Data Not Available</div>
  // }

  console.log(setLanguage)
  console.log(lang)
  return (
    <>
    <BreadCrumbs breadCrumbTitle='Quiz' breadCrumbParent='quiz' breadCrumbActive='Edit' />
    <Card className="p-2">
      {(Object.keys(quizDetailsData).length && (
        <Fragment>
        <div className="content-header">
          <h5 className="mb-0">Edit Quiz Details</h5>
          <small className="text-muted">Quiz Details.</small>
        </div>
        <div style={{display:"flex", justifyContent:"end"}}>
          {/* <ButtonDropdown isOpen={dropdownOpen} toggle={toggleDropdown}>
            <DropdownToggle color='info' caret>
              Language
            </DropdownToggle>
            <DropdownMenu name={"language_code"} value={language} onChange={(e) => setLanguage(e.target.value)}>
              {
                quizdet.translation_quiz.map((obj, i) => {
                  console.log(language)
                  return (
                    <DropdownItem key={i} value={obj.master_language_code} >
                      {obj.master_language_name}
                    </DropdownItem>
                  )
                })
              }
            </DropdownMenu>
          </ButtonDropdown>
          //////////////////////////////// */}
          {quizDetailsData.translation_quiz?.length > 1 ? 
    //                 <ButtonDropdown isOpen={dropdownOpen} toggle={toggleDropdown}>
    //   <DropdownToggle color='primary' caret>
    //     Language
    //   </DropdownToggle>
    //   <DropdownMenu name={"language_code"} value={language} >
    //           {
    //             quizDetailsData.translation_quiz.map((obj, i) => {
    //               return (
    //                 <DropdownItem key={i} value={obj.master_language_id} > 
    //                  {/* <DropdownItem key={i}   value={lang === obj.master_language_id ? obj.master_language_id : ""}  onChange={() => setLanguage }>  */}
    //                   {obj.master_language_name}
    //                 </DropdownItem>
    //               )
    //             })
    //           }
    //         </DropdownMenu>
    // </ButtonDropdown> 
    <span> <Button.Ripple color='primary' onClick={() => changelang(2)}>Arabic</Button.Ripple>  <Button.Ripple color='primary'  onClick={() => changelang(1)}>English</Button.Ripple></span>
    :   <Button.Ripple color='primary'>{quizDetailsData.translation_quiz[0]?.master_language_id === 1 ? 'English' : 'Arabic'}</Button.Ripple>}
          {/* <ButtonGroup className='mb-1'>
          {
            quizdet.translation_quiz?.map((obj, i) => {
              return (
                <Button color='info' onClick={() => setLanguage(obj.master_language_code)} key={i}>
                  {obj.master_language_name}
                </Button>
                )
            })
          }
          </ButtonGroup> */}
        </div>
        <Form onSubmit={handleSubmit(onSubmit1)}>
          <Row>
            <FormGroup tag={Col} md="6">
              <Label className="form-label" for="quiz_name">
                Name
              </Label>
              <Input
                name="quiz_name"
                id="quiz_name"
                placeholder="Quiz name"
                innerRef={register({ required: true })}
                className={classnames({
                  'is-invalid': errors['quiz_name']
                })}
              />
            </FormGroup>
            <FormGroup tag={Col} md="6">
              <Label className="form-label" for="quiz_category">
                Quiz Category
              </Label>
              <Controller
                isClearable
                as={Select}
                id="quiz_category"
                control={control}
                name="quiz_category"
                rules={{ required: true }}
                options={colourOptions}
                className={classnames('react-select', {
                  'is-invalid': errors['quiz_category']
                })}
                classNamePrefix="select"
                theme={selectThemeColors}
              />
            </FormGroup>
          </Row>
          <Row>
            <div className="form-group form-password-toggle col-md-6">
            <Label className="form-label" for="quiz_expiry">
                Expiry
              </Label>
              <Controller
                data-enable-time
                as={Flatpickr}
                control={control}
                id="quiz_expiry"
                name="quiz_expiry"
                rules={{ required: true }}
                className={classnames('form-control flatpickr-input', {
                  'is-invalid': errors['quiz_expiry']
                })}
              />
            </div>
            <div className="form-group form-password-toggle col-md-6">
              <Label className="form-label" for="quiz_coins">
                Quiz coins
              </Label>
              <Input
                type="number"
                name="quiz_coins"
                id="quiz_coins"
                innerRef={register({ required: true })}
                className={classnames({
                  'is-invalid': errors['quiz_coins']
                })}
              />
            </div>
          </Row>
          <div className="d-flex justify-content-end">
            {/* <Button.Ripple
              color="secondary"
              className="btn-prev"
              outline
              disabled
            >
              <ArrowLeft
                size={14}
                className="align-middle mr-sm-25 mr-0"
              ></ArrowLeft>
              <span className="align-middle d-sm-inline-block d-none">
                Previous
              </span>
            </Button.Ripple> */}
            <Button.Ripple type="submit" color="primary" className="btn-next">
              <span className="align-middle d-sm-inline-block d-none">
                Submit Quiz Details
              </span>
            </Button.Ripple>
          </div>
        </Form>
      </Fragment>
      )) || ''}
      
      {(quizQuestionsData.length && (
        <Fragment>
          <div className="content-header">
            <h5 className="mb-0">Edit Questions</h5>
            <small>Enter Questions.</small>
          </div>
          <Form onSubmit={handleSubmit(onSubmit2)}>
            <Repeater count={count}>
              {(i) => (
                <li key={i} className="list-unstyled">
                  <Row>
                    <FormGroup tag={Col} md="12">
                      <Label className="form-label" for={`question-${i}`}>
                        Quiz question
                      </Label>
                      <Input
                        type="text"
                        id={`question-${i}`}
                        name={`question-${i}`}
                        placeholder="Who is Lionel Messi?"
                        innerRef={register({ required: true })}
                        className={classnames({
                          'is-invalid': errors[`question-${i}`]
                        })}
                      />
                    </FormGroup>
                  </Row>
                  <Row>
                    <FormGroup tag={Col} md="8">
                      <Label className="form-label" for={`answer1-${i}`}>
                        Answer 1
                      </Label>
                      <Input
                        type="text"
                        name={`answer1-${i}`}
                        id={`answer1-${i}`}
                        placeholder="Cricketer"
                        innerRef={register({ required: true })}
                        className={classnames({
                          'is-invalid': errors[`answer1-${i}`]
                        })}
                      />
                    </FormGroup>
                    <FormGroup tag={Col} md="4">
                      <div className="d-flex height-95-per align-items-end">
                        <FormGroup check inline>
                          <Label check>
                            <Input
                              type="radio"
                              name={`correct-answer-${i}`}
                              defaulChecked
                              {...register(`correct-answer-${i}`)}
                              value="1"
                            />
                            Correct Answer
                          </Label>
                        </FormGroup>
                      </div>
                    </FormGroup>
                  </Row>
                  <Row>
                    <FormGroup tag={Col} md="8">
                      <Label className="form-label" for={`answer2-${i}`}>
                        Answer2
                      </Label>
                      <Input
                        type="text"
                        name={`answer2-${i}`}
                        id={`answer2-${i}`}
                        placeholder="Footballer"
                        innerRef={register({ required: true })}
                        className={classnames({
                          'is-invalid': errors[`answer2-${i}`]
                        })}
                      />
                    </FormGroup>
                    <FormGroup tag={Col} md="4">
                      <div className="d-flex height-95-per align-items-end">
                        <FormGroup check inline>
                          {/* <Label check>
                            <Input
                              type="radio"
                              name={`correct-answer-${i}`}
                              {...register(`correct-answer-${i}`)}
                              value="2"
                            />
                            Correct Answer
                          </Label> */}
                        </FormGroup>
                      </div>
                    </FormGroup>
                  </Row>
                  <Row>
                    <FormGroup tag={Col} md="8">
                      <Label className="form-label" for={`answer3-${i}`}>
                        Answer 3
                      </Label>
                      <Input
                        type="text"
                        name={`answer3-${i}`}
                        id={`answer3-${i}`}
                        placeholder="VolleyBaller"
                        innerRef={register({ required: true })}
                        className={classnames({
                          'is-invalid': errors[`answer3-${i}`]
                        })}
                      />
                    </FormGroup>
                    <FormGroup tag={Col} md="4">
                      <div className="d-flex height-95-per align-items-end">
                        <FormGroup check inline>
                          {/* <Label check>
                            <Input
                              type="radio"
                              name={`correct-answer-${i}`}
                              {...register(`correct-answer-${i}`)}
                              value="3"
                            />
                            Correct Answer
                          </Label> */}
                        </FormGroup>
                      </div>
                    </FormGroup>
                  </Row>
                  <Row>
                    <FormGroup tag={Col} md="8">
                      <Label className="form-label" for={`answer4-${i}`}>
                        Answer 4
                      </Label>
                      <Input
                        type="text"
                        name={`answer4-${i}`}
                        id={`answer4-${i}`}
                        placeholder="BasketBaller"
                        innerRef={register({ required: true })}
                        className={classnames({
                          'is-invalid': errors[`answer4-${i}`]
                        })}
                      />
                    </FormGroup>
                    <FormGroup tag={Col} md="4">
                      <div className="d-flex height-95-per align-items-end">
                        <FormGroup check inline>
                          {/* <Label check>
                            <Input
                              type="radio"
                              name={`correct-answer-${i}`}
                              {...register(`correct-answer-${i}`)}
                              value="4"
                            />
                            Correct Answer
                          </Label> */}
                        </FormGroup>
                      </div>
                    </FormGroup>
                  </Row>
                  <Row>
                    <FormGroup tag={Col} md="6">
                      <div className="d-flex justify-content-end">
                        <Button.Ripple
                          type="button"
                          color="primary"
                          className="btn-next"
                          onClick={() => {
                            submitQuestion(i)
                          }}
                        >
                          <span className="align-middle d-sm-inline-block d-none">
                            Submit Questions
                          </span>
                        </Button.Ripple>
                      </div>
                    </FormGroup>
                  </Row>
                </li>
              )}
            </Repeater>
            <div className="d-flex justify-content-between">
              {count <= 4 && (
                <Button.Ripple
                  className="btn-icon"
                  color="primary"
                  onClick={increaseCount}
                >
                  <Plus size={14} />
                  <span className="align-middle ml-25">Add New Question</span>
                </Button.Ripple>
              )}
            </div>
          </Form>
        </Fragment>
      )) ||
        ''}
    </Card>
    </>
  )
}

export default QuizEdit
